import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { GoogleSpreadsheet } from "google-spreadsheet";
import Plot from 'react-plotly.js';
import * as google_sheets from '../google_sheets.json';


// TODO(akadian): look into reusing styles code
const styles = theme => ({
    content: {
        margin: '0 auto',
        marginTop: '1.5em'
    },
    root: {
    },
    gridItem: {
        padding: theme.spacing(1.5),
    },
    sectionHeader: {
        marginTop: '0.15em',
    },
    container: {
        padding: theme.spacing(2),
    },
    footer: {
        marginTop: '0.15em',
        fontSize: 14,
    }
});

const SPREADSHEET_ID = google_sheets.SPREADSHEET_ID;
const SHEET_ID = google_sheets.SHEET_ID;
const CLIENT_EMAIL = google_sheets.CLIENT_EMAIL;
const PRIVATE_KEY = google_sheets.PRIVATE_KEY;

const appendSpreadsheet = async (row) => {
    const doc = new GoogleSpreadsheet(SPREADSHEET_ID);

    // TODO(akadian): update the formatting of below try-catch block

    try {
            await doc.useServiceAccountAuth({
            client_email: CLIENT_EMAIL,
            private_key: PRIVATE_KEY,
        });

        await doc.loadInfo();

        const sheet = doc.sheetsById[SHEET_ID];
        const result = await sheet.addRow(row);
    } catch (e) {
        console.error('Error: ', e);
    }
};

class Weight extends React.Component {
    constructor() {
        super();
        this.state = {
            p_date: [],
            p_weight: [],
            a_date: [],
            a_weight: [],
            form_name: "",
            form_date: "",
            form_weight: ""
        };

        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleDateChange = this.handleDateChange.bind(this);
        this.handleWeightChange = this.handleWeightChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleNameChange(event) {
        this.setState( {form_name: event.target.value} );
    }

    handleDateChange(event) {
        this.setState( {form_date: event.target.value} );
    }

    handleWeightChange(event) {
        this.setState( {form_weight: event.target.value} );
    }

    async handleSubmit(event) {
        const newRow = {
            Name: this.state.form_name,
            Date: this.state.form_date,
            Weight: this.state.form_weight
        };
        appendSpreadsheet(newRow);
        alert('Data submitted, refresh page to update graphs');
        event.preventDefault();
    }

    async componentDidMount() {
        const doc = new GoogleSpreadsheet(SPREADSHEET_ID);

        var p_date = [];
        var p_weight = [];
        var a_date = [];
        var a_weight = [];

        await doc.useServiceAccountAuth({
            client_email: CLIENT_EMAIL,
            private_key: PRIVATE_KEY,
        });
        await doc.loadInfo();
        const sheet = doc.sheetsById[SHEET_ID];
        const rows = await sheet.getRows();

        for (var i = 0; i < rows.length; i++) {
            if (rows[i].Name === "P") {
                p_date.push(rows[i].Date);
                p_weight.push(rows[i].Weight);
            } else if (rows[i].Name === "A") {
                a_date.push(rows[i].Date);
                a_weight.push(rows[i].Weight);
            }
        }

        this.setState(
            {
                p_date: p_date,
                p_weight: p_weight,
                a_date: a_date,
                a_weight: a_weight
            }
        )
    }

    render() {
        const { classes } = this.props;
        document.title='Weight';

        return (
            <div className={classes.root}>
                <Grid
                    container
                    justify="center"
                    alignContent="center"
                >
                    <Grid
                        item
                        xs={10}
                        md={9}
                        lg={9}
                        className={classes.content}
                    >
                        <Grid item xs={12} md={12} lg={12} className={classes.gridItem}>
                            <Typography
                                className={classes.sectionHeader}
                                variant="h5"
                                gutterBottom
                                align="center"
                                >
                                Weight Tracking
                            </Typography>
                        </Grid>

                        <Plot
                            data={[
                            {
                                x: this.state.p_date,
                                y: this.state.p_weight,
                                type: 'scatter',
                                mode: 'lines+markers',
                                marker: {color: 'orange'},
                                name: 'P'
                            },
                            {
                                x: this.state.a_date,
                                y: this.state.a_weight,
                                type: 'scatter',
                                mode: 'lines+markers',
                                marker: {color: 'blue'},
                                name: 'A'
                            },
                            {
                                x: [
                                    '2020-09-21', '2020-10-01', '2020-10-11', '2020-10-21',
                                    '2020-11-01', '2020-11-11', '2020-11-21', '2020-12-01',
                                    '2020-12-11', '2020-12-15'
                                ],
                                y: [
                                    58, 58, 58, 58,
                                    58, 58, 58, 58,
                                    58, 58
                                ],
                                type: 'scatter',
                                mode: 'lines',
                                marker: {color: 'rgba(255, 165, 0, 0.3)'},
                                name: 'Target-P'
                            },
                            {
                                x: [
                                    '2020-09-21', '2020-10-01', '2020-10-11', '2020-10-21',
                                    '2020-11-01', '2020-11-11', '2020-11-21', '2020-12-01',
                                    '2020-12-11', '2020-12-15'
                                ],
                                y: [
                                    72, 72, 72, 72,
                                    72, 72, 72, 72,
                                    72, 72
                                ],
                                type: 'scatter',
                                mode: 'lines',
                                marker: {color: 'rgba(0, 0, 255, 0.3)'},
                                name: 'Target-A'
                            },
                            ]}
                            layout={
                                {
                                    'xaxis': {
                                        'title': 'date'
                                    },
                                    'yaxis': {
                                        'title': 'weight(kg)',
                                        'range': [55, 80]
                                    },
                                    'shapes': [
                                        {
                                            'type': 'line',
                                            'x0': '2020-09-21',
                                            'x1': '2020-12-15',
                                            'y0': 58,
                                            'y1': 58,
                                            'line': {
                                                'color': 'rgba(255, 165, 0, 0.3)',
                                                'width': 3,
                                            },
                                        }
                                    ],
                                    'showlegend': true
                                }
                            }
                            // TODO(akadian): look into specifying layout width and height
                            // TODO(akadian): specify ggplot2 template
                        />

                        <form onSubmit={this.handleSubmit}>
                            <label>
                                Name &nbsp;
                                <input type="text" value={this.state.form_name} name="name" onChange={this.handleNameChange} />
                            </label>
                            <br/><br/>
                            <label>
                                Date (YYYY-MM-DD) &nbsp;
                                <input type="text" value={this.state.form_date} name="date" onChange={this.handleDateChange} />
                            </label>
                            <br/><br/>
                            <label>
                                Weight (kg) &nbsp;
                                <input type="text" value={this.state.form_weight} name="weight" onChange={this.handleWeightChange} />
                            </label>
                            <br/><br/>
                            <input type="submit" value="Submit" />
                        </form>

                    </Grid>
                </Grid>
            </div>
        );
    }
}

Weight.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Weight);
